/*star*/
#particles-js {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

/*pace*/
.page-loaded .bg_bark {
  display: block;
  width: 100vw;
  height: 0;
  background: url(../images/bg.png) repeat-x center 0;
  background-position: 0 center;
  background-size: 100% 100%;
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  transition: all .0s;
}

.page-loaded #particles-js {
  transform: scale(0);
  transform-origin: center center;
  transition: all .0s .0s;
}

.page-loaded .bg_line_str {
  transform: translate3d(0px, -100%, 0);
  transition: all .0s .0s;
}

.page-loaded .loading_bar {
  display: block;
  width: 240px;
  height: 32px;
  background-image: url(../images/bg_loading.jpg);
  background-repeat: repeat-x;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -120px;
  margin-top: -15px;
  z-index: 100;
  overflow: hidden;
  animation-duration: 10s;
  animation-name: move_bg;
  transform-origin: left center;
  animation-iteration-count: infinite;
  transition: opacity .3s;
}

.page-loaded .loading_bar img {
  display: block;
  width: 96%;
  height: auto;
  margin: 1% 2%;
}

.page-loaded .wrap {
  opacity: 0;
  transition: opacity .0s .0s;
  transition-timing-function: ease-in-out;
}

.page-loaded.pace-done .bg_bark {
  height: 100vh;
  top: 0%;
  transition: all .3s;
}

.page-loaded.pace-done #particles-js {
  transform: scale(1);
  transition: all .6s .4s;
}

.page-loaded.pace-done .bg_line_str {
  transform: translate3d(0px, 0%, 0);
  transition: all .6s .3s;
}

.page-loaded.pace-done .loading_bar {
  opacity: 0;
  visibility: hidden;
}

.page-loaded.pace-done .wrap {
  opacity: 1;
  transition: all .6s 1s;
}

.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.pace.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background: #00B9B4 url(../images/bg_loading.jpg) no-repeat left center;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 4px;
  -webkit-box-shadow: 5px 0 10px #00B9B4, 5px 0 5px #00B9B4;
  box-shadow: 5px 0 10px #00B9B4, 5px 0 5px #00B9B4;
}

.pace .pace-activity {
  background: #999;
  position: fixed;
  z-index: 1999;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 2px;
}

.bg_line_str {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background: url(../images/bg_line.png) repeat 0 0;
}

/* HEADER
====================== */
.header {
  height: 0;
}

.header .header_menu {
  width: 170px;
  margin-top: 85px;
  position: fixed;
  top: 50%;
  left: 30px;
  transform: rotate(270deg);
  transform-origin: left center;
  z-index: 100;
}

.header .header_menu li {
  display: inline-block;
  margin: 0 0px;
}

.header .header_menu li a {
  display: block;
  position: relative;
  bottom: 0;
  padding: 0px;
  background-color: transparent !important;
  color: #fff;
}

.header .header_menu li a:hover, .header .header_menu li a:active {
  color: #00B9B4;
}

.header .header_menu li a:after {
  display: block;
  content: '';
  width: 0;
  height: 1px;
  background-color: #00B9B4;
  position: absolute;
  left: 50%;
  bottom: 0;
  border-color: #00B9B4;
  transition: all ease-out 0.3s;
}

.header .header_menu li a:hover:after,
.header .header_menu li a .active:after {
  width: 100%;
  margin-left: -50%;
}

.header .header_menu li.active a {
  color: #00B9B4;
}

.header .header_menu li.active a:after {
  width: 100%;
  margin-left: -50%;
}

.header .header_info {
  width: 300px;
  margin-top: 150px;
  position: fixed;
  top: 50%;
  right: 30px;
  transform: rotate(90deg);
  transform-origin: right center;
  z-index: 100;
}

.header .header_info li {
  margin: 0 0px;
}

.header .header_info li a:hover {
  color: #00B9B4;
}

.header .header_info li a.c_mail:hover {
  color: #0067b8;
}

.header .header_info li a.c_line:hover {
  color: #00c300;
}

.header .header_info li a.c_fb:hover {
  color: #3b5998;
}

