====================== */
.sec_ban .wheel {
  display: block;
  width: 700px;
  height: 700px;
  margin-top: -350px;
  margin-left: -350px;
  background: url(../images/cyc_003.png) no-repeat center center;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 500;
  border-radius: 50%;
  z-index: 1;
}

.sec_ban .wheel:before {
  display: block;
  content: '';
  width: 700px;
  height: 700px;
  background: url(../images/cyc_001.png) no-repeat center center;
  position: absolute;
  top: 0px;
  left: 0px;
  animation-duration: 6s;
  animation-name: rotate_360;
  animation-timing-function: cubic-bezier(0.77, 0, 0.18, 1);
  animation-iteration-count: infinite;
}

.sec_ban .wheel:after {
  display: block;
  content: '';
  width: 700px;
  height: 700px;
  background: url(../images/cyc_002.png) no-repeat center center;
  position: absolute;
  top: 0px;
  left: 0px;
  animation-duration: 1s;
  animation-name: oclock;
  animation-timing-function: cubic-bezier(0.79, 0.14, 0.15, 0.86);
  animation-iteration-count: infinite;
}

.sec_ban .wheel .wheel_point {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}

.sec_ban .wheel.active {
  opacity: 0;
  filter: Alpha(Opacity=0);
  transform: translate3d(0%, -100px, 0);
