/* LIGHTBOX
====================== */
.lightbox {
  display: block;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.66);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 0;
  user-select: none;
  text-size-adjust: none;
}

.lightbox .lightbox_wrap {
  display: none;
  width: 100%;
  height: 100%;
  padding: 0px;
  opacity: 0;
  overflow: hidden;
}

.lightbox .lightbox_wrap.active {
  display: block;
  opacity: 1;
}

.lightbox .lightbox_wrap #lightBoxList {
  height: 100vh;
}

.lightbox .lightbox_wrap .list {
  display: block;
  padding-bottom: 60px;
}

.lightbox .lightbox_wrap .list .item {
  display: block;
}

.lightbox .lightbox_wrap .list .item a {
  display: block;
  padding: 5px 15px 20px;
}

.lightbox .lightbox_wrap .list .item a .mun {
  display: inline-block;
  width: 52px;
  height: 52px;
  line-height: 52px;
  color: #00B9B4;
  font-family: "font_BNKGOTHL", Arial, "微軟正黑體", "Apple LiGothic", sans-serif;
  font-size: 24px;
  text-align: center;
  border: 2px solid #00B9B4;
  position: relative;
  top: -6px;
}

.lightbox .lightbox_wrap .list .item a .title {
  display: inline-block;
  height: 52px;
  margin: 0px 15px 0;
}

.lightbox .lightbox_wrap .list .item a .title h1 {
  display: inline-block;
  line-height: 52px;
  color: #fff;
}

.lightbox .lightbox_wrap .list .item a .title small {
  display: inline-block;
  line-height: 52px;
  padding-left: 10px;
  color: #999;
}

.lightbox .lightbox_wrap .list .item a:hover {
  background-color: #000;
}

.lightbox .lightbox_wrap .list .item a:hover .title h1 {
  color: #fff;
}

.lightbox .lightbox_wrap .list .item a:hover .title small {
  color: #999;
}

.lightbox .lightbox_wrap .list .item:first-child {
  margin-top: 5%;
}

.lightbox .btn_close {
  display: block;
  width: 52px;
  height: 52px;
  background-color: #3D3D3F;
  border: 0 none;
  position: fixed;
  top: 15px;
  right: 15px;
  z-index: 5;
}

.lightbox .btn_close .close {
  opacity: 1;
}

.lightbox .btn_close .close .lines {
  margin: -10px 0 0 -10px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  overflow: hidden;
  transform: rotate(45deg);
}

.lightbox .btn_close .close .lines .line {
  display: block;
  position: absolute;
  background: #fff;
  transition: transform .5s 0s;
}

.lightbox .btn_close .close .lines .line:nth-child(1) {
  top: 0;
  left: 50%;
  width: 1px;
  height: 100%;
  transform: translate(0px, -20px);
}

.lightbox .btn_close .close .lines .line:nth-child(2) {
  top: 50%;
  right: 0;
  width: 100%;
  height: 1px;
  transform: translate(-20px, 0px);
}

.lightbox.active {
  z-index: 100;
}

.lightbox.active .btn_close .close .lines .line:nth-child(1) {
  transform: translate(0px, 0px);
  transition-delay: .6s;
}

.lightbox.active .btn_close .close .lines .line:nth-child(2) {
  transform: translate(0px, 0px);
  transition-delay: .6s;
}

.lightbox.active.closeing .btn_close .close .lines .line:nth-child(1) {
  transform: translate(0px, -20px);
  transition-delay: 0s;
}

.lightbox.active.closeing .btn_close .close .lines .line:nth-child(2) {
  transform: translate(-20px, 0px);
  transition-delay: 0s;
}

